<template>
  <div class="activeDirectory">
    <div class="container">
      <div class="activeTitle">
        活动列表
      </div>
      <div class="activeContent">
        <div class="activeAdd" @click="addActivity">
          添加活动
          <span>+</span>
        </div>
        <div class="activeList">
          <ul>
            <li v-for="(item,index) in activeData" :key="index" @click="EnterDetail(item.activityId,item.activityName)" :class="{commonClass2:judgeStatus(item.startDate,item.endDate)=='已结束'}">
              <div class="activeName">
                <p>活动名称</p>
                <p>{{item.activityName}}</p>
              </div>
              <div class="activeStatus">
                <p>状态</p>
                <p>{{judgeStatus(item.startDate,item.endDate)}}</p>
              </div>
              <div class="activeTime">
                 {{item.startDate | getdate}}~{{item.endDate | getdate}}
              </div>
              <div class="closeBtn" @click.stop="showConfimBox(item.activityId)">
                <i class="el-icon-close"></i>
              </div>
              <div class="etiorBtn" @click.stop="EnterEditor(item)">
                <img src="../../assets/images/edit.png" alt="">
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
.activeDirectory{
  text-align: left;
  .container{
    margin:60px 0 60px 260px;
    .activeTitle{
  	  color: #808080;
  	  height: 58px;
  	  font-size: 14px;
  	  line-height: 58px;
  	  padding-left: 50px;
  	  border-bottom: 1px solid #eee;
    }
    .activeContent{
      padding: 35px 58px;
      .activeAdd{
        width: 118px;
        height: 36px;
        background-color: #4cb4e7;
        border: 1px solid #4cb4e7;
        font-size: 14px;
        color: #f5f5f5;
        text-align: center;
        line-height: 36px;
        cursor: pointer;
        span{
          margin-left: 12px;
        }
      }
      .activeList{
        margin-top: 47px;
        ul{
          li{
            width: 285px;
            height: 150px;
            float: left;
            margin-right: 60px;
            margin-bottom: 50px;
            font-size: 14px;
            color: #707070;
            background-color: #ffffff;
            box-shadow:0 3px 6px rgba(0, 0, 0, .16);
            padding: 22px 15px 10px 15px;
            border: 1px solid #e8e8e8;
            cursor: pointer;
            position: relative;
            .activeName p:last-child,
            .activeStatus p:last-child{
              font-size: 15px;
              font-weight: 700;
              color: #707070;
              margin-top: 5px;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
            .activeStatus{
              margin: 15px 0;
            }
            .closeBtn{
              width: 15px;
              height: 15px;
              position: absolute;
              right: 14px;
              top:11px;
              .el-icon-close{
                font-weight: bold;
              }
            }
            .etiorBtn{
              width: 31px;
              height: 31px;
              text-align: center;
              line-height: 26px;
              position: absolute;
              right: 44px;
              top:50%;
              margin-top: -15px;
              border-radius: 50%;
              border: 1px solid #707070;
              img{
                width: 18px;
                height: 16px;
              }
            }
          }
        }
      }
    }
  }
}
</style>
<script>
export default {
  name:'activeDirectory',
  data(){
    return{
      activeData:[],//活动数据
    }
  },
  created() {
    this.getActivityPager()
  },
  methods: {
    addActivity(){
      this.$router.push({path:'/activeAdd',query:{title:'添加活动'}})
    },
    EnterEditor(item){
      this.$router.push({path:'/activeAdd',query:{title:'编辑活动',activeData:item}})
    },
    EnterDetail(activityId,activityName){
      this.$router.push({path:'/activeDetails',query:{activityId:activityId,title:activityName}})
    },
    //获取活动列表
    getActivityPager(){
      let url = this.url + this.net.queryActivityPager;
      let params = {
        "pageNum":1,
        "pageSize":100,
        "orderByParam":"{createTime:-1}"
      }
      this.$post(url,params).then((res)=>{
        if(res.code == 200 && res.data.result != null && res.data.result != 'null'){
          this.activeData = res.data.result;
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
    //判断状态
    judgeStatus(starTime,endTime){
      let nowTime = new Date().getTime();
      if(starTime>nowTime){
        return '未开始'
      }else if(endTime > nowTime){
        return '进行中'
      }else if(endTime < nowTime){
        return '已结束'
      }
    },
    //删除活动
    DeleteItems(id){
      let url = this.url + this.net.removeActivity;
      let params = {
        "activityId":id,
      }
      this.$post(url,params).then((res)=>{
        if(res.code == 200 && res.data == true){
          this.activeData.forEach((item,index) => {
            if(item.activityId == id){
              this.activeData.splice(index,1)
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          });
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
    showConfimBox(id){
      this.$confirm('此操作将永久删除该活动, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.DeleteItems(id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    }
  },
  components:{

  }
}
</script>


